body {
  margin: 0;  
  background-color: hsl(240, 80%, 12%);
  font-family: 'Mountains of Christmas', cursive;
  font-size: 20px;
}

main, [role=main] {
  position: fixed;
  top: 0;
  left: 0;
  z-index: -10;
}
button {
  font-family: 'Mountains of Christmas', cursive;
  font-size: 1.5rem;
  padding: 0 1em;
  border-radius: 1em;
  box-shadow: 10px 10px 10px hsla(0, 0%, 0%, 0.5);
  background-color: #c00;
  color: white;
  border: 0;
}
button:active {
  box-shadow: 5px 5px 10px hsla(0, 0%, 0%, 0.5);
}

.info {
  background: #fffff0;
  position: relative;
  z-index: 100;
  max-width: 500px;
  margin-left: auto;
  margin-right: auto;
  margin-top: 100px;
  text-align: center;
  padding: 2em;
  border-radius: 2em;
  box-shadow: 10px 10px 10px hsla(0, 0%, 0%, 0.5);
}

[data-small-image] {
  width: 200px;
}

[data-hidden] {
  display: none;
}

[data-position="BR"] {
  position: fixed;
  right: 30px;
  bottom: 30px;
}
[data-position="BR"]:active {
  bottom: 25px;
}

[data-position="BL"] {
  position: fixed;
  left: 30px;
  bottom: 30px;
}
[data-position="BL"]:active {
  bottom: 25px;
}

[data-position="TR"] {
  position: fixed;
  top: 30px;
  right: 30px;
}
[data-position="TR"]:active {
  top: 35px;
}

@media only screen and (min-width: 600px) {
  body {
    font-size: 25px;
  }
  button {
    font-size: 2rem;
  }
}
